import { graphql } from "gatsby"
import React from "react";
import Layout from "../layout";
import CommonHead from "../layout/components/Head";

const PageTemplate = (props) => {
  return (
    <Layout {...props} />
  )
};

export function Head(props) {
  return <CommonHead {...props} />;
}

export const query = graphql`
  query Page($id: String!, $albumTitle: String) {
    page: googleDocs(path: {eq: $id}) {
      name
      menu
      childMarkdownRemark {
        htmlAst
      }
      description
      affiliate
      modifiedTime

      cover {
        image {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
      showTitle
    }

    allGooglePhotosPhoto(filter: {album: {title: {eq: $albumTitle} }}) {
      nodes {
        id
        file {
          childImageSharp {
            gatsbyImageData
          }
        }
        album {
          title
        }
        description
      }
    }
  }
`;

export default PageTemplate;